<template>
  <div id="app">
    <template v-if="isLoggedIn">
      <MenuBarComponent/>
      <div class="admin-panel-choice-menu">
        <div class="choise-block "> <span>Вікна:</span> </div>
        <div class="choise-block button-choice" @click="openedWindow = 'manager'" :class="openedWindow == 'manager' ? 'is-active':''"><span>Менеджер</span> </div>
        <div class="choise-block button-choice" @click="openedWindow = 'coordinator'" :class="openedWindow == 'coordinator' ? 'is-active':''"><span>Координатор</span></div>
        <div class="choise-block button-choice" @click="openedWindow = 'repairman'" :class="openedWindow == 'repairman' ? 'is-active':''"><span>Ремонтник</span></div>
      </div>
      <UsersManagement v-if="openedWindow == 'manager'"/>
      <RepairCoordinatorPanel v-if="openedWindow == 'coordinator'"/>
      <RepairmanPanel v-if="openedWindow == 'repairman'"/>
      
    </template>
    <AuthorizationAndRegistration v-if="!isLoggedIn"/>
  </div>
</template>

<script>
import AuthorizationAndRegistration from "./components/AuthorizationAndRegistration.vue";
import MenuBarComponent from "./components/MenuBarComponent.vue";
import UsersManagement from "./components/UsersManagement.vue";
import RepairCoordinatorPanel from "./components/RepairCoordinatorPanel.vue";
import RepairmanPanel from "./components/RepairmanPanel.vue";

export default {
  name: "App",

  components: {
    
    AuthorizationAndRegistration,
    MenuBarComponent,
    UsersManagement,
    RepairCoordinatorPanel,
    RepairmanPanel,
  },

  data() {
    return {
      openedWindow: '',
      userInfo: ''
    };
  },

  methods: {
  },

  computed: {
    isLoggedIn() {
      return this.$store.getters['isLoggedIn'];
    },
    getUserInfo() {
      return this.$store.getters['getUserInfo'];
    }
  },

  mounted() { 
    this.userInfo = this.getUserInfo;
    console.log(this.userInfo)
    if(localStorage.getItem('uid')){
      this.$store.dispatch('setAccountInfo', localStorage.getItem('uid')).then(()=>{
        this.userInfo = this.getUserInfo;
      });
    }
    
  },
  
};
</script>

<style lang="less">
  @import "./assets/less/index.less";
</style>
