<template>
  <div class="repairman-panel-container">
    
    <div class="window-repair-panel-title">
      <div class="empty-block"  v-if="!selectRepairBaseIsOpen"></div>
      <h2 class="repairbase-panel-title">Ремонтна база</h2>
      <div @click="selectRepairBaseIsOpen = true" class="hide-repair-base-select" v-if="!selectRepairBaseIsOpen">
        <img src="../assets/images/dropdown-icon.png" alt="">
      </div>
    </div>
    <div v-if="selectRepairBaseIsOpen" class="select-repairbase-container">
      <div class="empty-block" ></div>
        <div class="custom-select" >
          <div class="selected-option" @click="openCloseRepairBaseSelect('selected-repair-base')" :class="selectFilterIsOpen == 'selected-repair-base' ? 'selected-option-is-active' : ''"><span>{{allRepairBases[selectedChoice]? (allRepairBases[selectedChoice].name + " " + allRepairBases[selectedChoice].adress) : 'База ремонту не обрана'}}</span><img src="../assets/images/dropdown-icon.png" :class="selectFilterIsOpen == 'selected-repair-base' ? 'dropdown-active-icon' : ''" alt=""></div>
            <div class="options-container" v-if="selectFilterIsOpen == 'selected-repair-base'">
              <template v-for="repairBase in allRepairBases">
              <div class="option" :key="repairBase.id" @click="makeChoice('selected-repair-base', repairBase.id)">
                <span class="repair-base-info">{{ allRepairBases[repairBase.id].name + " " + allRepairBases[repairBase.id].adress }}  </span>
              </div>
            </template>
          </div>
        </div>
        <div @click="selectRepairBaseIsOpen = false" class="hide-repair-base-select ">
          <img class="close-repair-base-select" src="../assets/images/dropdown-icon.png" alt="">
        </div>
      </div>
     
      <div class="repair-base-content-container" v-if="selectedChoice">
        <div class="repair-base-control-panel-container">
          <button @click="filterStateChoice = 'beingRepaired'">Ремонтуються</button>
          <button @click="filterStateChoice = 'sentToRepair'">Мають прибути</button>
          <input type="text" placeholder="Пошук..." v-model="searchQuery" name="" id="">
        </div>
        <div class="repair-base-cars-container">
          <template v-for="car in filteredCars">
            <div class="car-card" :key="car.licensePlate">
              <div class="driver-info" v-if="driverInfoIsOpen == car.driver">
                <h2 class="driver-info-title">Інформація про водія </h2>
                <ul>
                  <li class="driver-info-text">ФІО: {{ getDriverFullNameByUid(car.driver)}}</li>
                  <li class="driver-info-text">Пошта: {{ allDrivers[driverInfoIsOpen]?.email }}</li>
                  <li class="driver-info-text">Номер: {{ allDrivers[driverInfoIsOpen]?.phoneNumber ? allDrivers[driverInfoIsOpen].phoneNumber : 'Не вказаний'}}</li>
                </ul>
              </div>
              <h2 class="car-info-title">{{ car.model }}</h2>
              <ul>
                <li class="car-info-text">Ліцензійний номер: {{ car.licensePlate }}</li>
                <li class="car-info-text">Пробіг: {{ car.mileage}} км</li>
                <div class="driver-info-open-button-field">
                  <span class="driver-fio">Водій: {{ getDriverFullNameByUid(car.driver)}} </span>
                  <div class="driver-info-button" @mouseover="driverInfoIsOpen = car.driver" @mouseout="driverInfoIsOpen = ''"><span>?</span> </div>
                </div>
              </ul>
              <div class="control-repair-btn-container">
                <button class="control-repair-car-button" @click="carFinishedRepair(car.licensePlate)" v-if="filterStateChoice == 'beingRepaired'">Закінчити ремонт</button>
                <button class="control-repair-car-button" @click="carArrivedToRepairBase(car.licensePlate)" v-if="filterStateChoice == 'sentToRepair'">Машина прибула</button>
              </div>
            </div>
          </template>
          <div class="drivers-not-found">{{ getDriverNotFoundMessage }}</div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
name: 'RepairBasePanel',

props: {
  
},

data() {
    return {
        allRepairBases: {},
        selectFilterIsOpen: '',
        selectedChoice: '',
        allCars: {},
        allDrivers: {},
        searchQuery: '',
        filterStateChoice: 'beingRepaired',
        driverInfoIsOpen: '',
        selectRepairBaseIsOpen: true,
    };
  },

  methods: {
    openCloseRepairBaseSelect(filterName){
      if(this.selectFilterIsOpen == filterName)
        this.selectFilterIsOpen = ''
      else
        this.selectFilterIsOpen = filterName;
    },
    makeChoice(filterName, value){
      this.selectedChoice = value;
      this.openCloseRepairBaseSelect(filterName);
    },
    getDriverFullNameByUid(uid){
      const selectedDriver = this.allDrivers[uid];
      if (selectedDriver) {
        const { firstname, lastname, surname } = selectedDriver;
        return `${lastname ? lastname + ' ' : ''}${firstname}${surname ? ' ' + surname : ''}`;
      } else {
        return 'Без водія';
      }
    },
    carArrivedToRepairBase(licensePlate){
      this.$set(this.allCars[licensePlate], 'state', 'beingRepaired');
      this.$store.dispatch('updateCarState', {licensePlate: licensePlate, state: 'beingRepaired'})
    },
    carFinishedRepair(licensePlate){
      this.$delete(this.allCars, licensePlate);
      this.$store.dispatch('updateCarState', {licensePlate: licensePlate, state: 'inUse'})
    },
   
  },

  computed: {
    filteredCars() {
      const query = this.searchQuery.toLowerCase().trim();
      var firstFilteredCars = Object.values(this.allCars).filter(car => {
          return (car.repairBase == this.selectedChoice) && (car.state == this.filterStateChoice);
        });

      if (!query) {
        return firstFilteredCars;
      }

      return Object.values(this.allCars).filter(car => {
        const searchableFields = ['model', 'repairBase', 'licensePlate', 'state', 'driver'];

        return searchableFields.some(field => {
          if (field === 'driver') {
            const driverName = this.getDriverFullNameByUid(car[field]);
            return driverName.toLowerCase().includes(query);
          } else {
            return car[field] && car[field].toString().toLowerCase().includes(query);
          }
        });
      });
    },
    getDriverNotFoundMessage(){
      var choiceStateFilteredCars = Object.values(this.allCars).filter(car => {
        return (car.repairBase == this.selectedChoice) && (car.state == this.filterStateChoice);
      });
      if(!choiceStateFilteredCars.length){
        if(this.filterStateChoice == 'beingRepaired'){
          return 'Машин, що ремонтуються - немає';
        }else{
          return 'Машин, що мають прибути - немає';
        }
      }else if(this.searchQuery && !this.filteredCars.length){
        return 'Співпадінь не знайдено'
      }else{
        return ''
      }
    }
  },

  mounted() {
    this.$store.dispatch('getAllRepairBases').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var repairBase = result.data();
        this.$set(this.allRepairBases, result.id, {id: result.id, name: repairBase.name, adress: repairBase.adress});
      });
    });
    this.$store.dispatch('getAllBrokenCars').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var car = result.data();
        this.$set(this.allCars, car.licensePlate, car);
      });
      console.log(this.allCars)
    });
    this.$store.dispatch('getAllDrivers').then((querySnapshot)=>{
      querySnapshot.forEach((result) => {
        var driver = result.data();
        this.$set(this.allDrivers, driver.uid, driver);
      });
      console.log(this.allDrivers)
    });
  },
}
</script>